import {request} from './request'

export function getBooking(page,time=0) {
  return request({
    //url:'/test/getBanners'
	//url:'/vue/VueProjectData/home/product.php'
	url:'/vue/VueProjectData/vantmachine/getbookings.php',
    params:{
		page,
		time,
    }
  });
}

