<template>
<div id="ubscribe">
	<van-nav-bar
		title="预约"
		left-text="返回"
		right-text="按钮"
		left-arrow
		@click-right="onClickRight"
		@click-left="onClickLeft"
		class="nav"
	/>
	<div class="van-cell-box">
		<van-cell-group :style="{background:'#f1f1f1'}">
			<div class="top10box">
			
			<van-field label="机器名称" :value="machine.machinename" readonly />
			<van-field label="机器编号" :value="machine.machinenum" readonly />
			<van-cell title="选择日期" :value="date" @click="show = true"  />
			<van-calendar v-model="show" @confirm="onConfirm" />
			<van-cell title="选择时间" :value="currentTime" @click="showPopup"  />
			<van-field
				v-model="fieldValue"
				is-link
				readonly
				label="地区"
				placeholder="请选择所在地区"
				@click="cshow = true"
			/>
				<van-popup v-model="cshow" round position="bottom">
			<van-cascader
				v-model="cascaderValue"
				title="请选择所在地区"
				:options="options"
				@close="cshow = false"
				@finish="onFinish"
			/>
			
			</van-popup>
			</div>
			<div class="top10box">
			<van-field v-model="bookingObj['contact']" label="联系人" placeholder="请输入联系人" />
				<van-field v-model="bookingObj['telephone']" label="联系人电话" placeholder="请输入联系人电话" />
				<van-field v-model="bookingObj['productname']"  label="产品名称" placeholder="请输入产品名称" />
			</div>
			<div class="top10box">
			<van-field
				v-model="bookingObj['bookmark']"
				rows="2"
				autosize
				label="备注"
				type="textarea"
				maxlength="50"
				placeholder="请输入留言"
				show-word-limit
			/>
			</div>
		</van-cell-group>
	</div>
	<div class="btn-box"><van-button type="primary" color='#516df5' :style="{width:'90%'}" @click="bookingClick">预约</van-button></div>
	<van-dialog v-model="dshow"  @confirm="dconfirm" show-cancel-button confirmButtonText="完成" confirmButtonColor="#80c269" cancelButtonColor="#ccc">
		<div class="show-cancel-box">
			<img src="~assets/images/common/yuyue.svg"/>
			<h3>预约成功</h3>
			<p>您的预定已经确认</p>
		</div>
	</van-dialog>
	<van-popup v-model="pshow" position="bottom" :style="{ height: '50%' }" >
		<van-datetime-picker
			v-model="currentTime"
			type="time"
			title="选择时间"
			@confirm="confirm"
			@cancel="cancel"
			:min-hour="0"
			:max-hour="23"
		/>
	</van-popup>

</div>
	
</template>

<script>
import { NavBar,Calendar,Cell,CellGroup,Field,Button,Dialog,DatetimePicker,Popup,Cascader } from 'vant';		//这个可以封装
import {addBooking} from 'network/booking'
export default {
  name: 'Subscribe',
	components:{
		[NavBar.name]:NavBar,
		[Cell .name]:Cell ,
		[Calendar .name]:Calendar ,
		[CellGroup  .name]:CellGroup  ,
		[Field   .name]:Field   ,
		[Button   .name]:Button   ,
		[Popup   .name]:Popup   ,
		[Cascader    .name]:Cascader    ,
		[DatetimePicker   .name]:DatetimePicker   ,
		[Dialog.Component    .name]:Dialog.Component    ,
	},
	data(){
		return{
				// 选项列表，children 代表子选项，支持多级嵌套
			options: [
				{
					text: '广东省',
					value: '330000',
					children: [
						{ text: '广州市', value: '320100' },
						{ text: '肇庆市', value: '320200',children:[
							{ text: '高要区', value: '320300' },
						]},
					],
				},
				{
					text: '台湾省',
					value: '320000',
					children: [
						{ text: '高雄市', value: '320100' },
						{ text: '台北市', value: '320300' },
					],
				},
			],
			currentTime: '12:00',
			date: '',
			show: false,
			cshow: false,
			pshow: false,
			fieldValue:'',
			cascaderValue:'',
			dshow: false,
			bookingObj:{
				"contact":'',
				"telephone":'',
				"productname":'',
				"bookmark":'',
				"bookingDate":'',
				"bookingTime":'',
				"address":'',
				"machineid":'',
			},
			machine:'',
		}
	},
	methods:{
		onClickRight(){
			this.$Toast('按钮')
		},
		onClickLeft(){
			this.$Toast('返回')
			this.$router.go(-1)
		},
		formatDate(date) {
			if(date.getDate()==13){
				this.$Toast('预约满了')
			}
			return `${date.getMonth() + 1}/${date.getDate()}`;
		},
		//日期确定
		onConfirm(date) {
			this.show = false;
			this.date = this.formatDate(date);
			this.bookingObj['bookingDate']=date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()
			console.log(this.date)
		},
		//预约确定按钮
		bookingClick(){
			//次按钮点击需要对数据进行一下过滤-发送的时候拼接一下时间
			//console.log(this.bookingObj)
			//这里发送数据
			addBooking(this.bookingObj).then(res=>{
				if(res.status==0){
					this.dshow=true			//弹窗的
				}else{
					this.$Toast(res.message)
				}
				console.log(res)
			})
	
		},
		//时间选择
		showPopup(){
			this.pshow=true
		},
		//时间选择确认
		confirm(){
			this.bookingObj['bookingTime']=this.currentTime
			console.log(this.currentTime)
			this.pshow=false
			
		},
		//时间选择取消
		cancel(){
			console.log("取消")
			//console.log(this.currentTime)
			this.pshow=false	
		},
		// 全部选项选择完毕后，会触发 finish 事件
		onFinish({ selectedOptions }) {
			this.cshow = false;
			this.fieldValue = selectedOptions.map((option) => option.text).join('/');
			this.bookingObj.address=this.fieldValue
		},
		dconfirm(){
			this.$router.push('category').catch(res=>{res})
		}
	},
	created(){
		this.machine=JSON.parse(localStorage.getItem('machine'))
		this.bookingObj.machineid=this.machine.id
		console.log(this.machine)
	}
}
</script>

<style scoped>
.top10box{
	margin-top:10px;
}
#ubscribe{
	height:100vh;
}
.nav{
	position:relative;
	top:0;
	left:0;
	background:#516df5;
}
