background:#f1f1f1;
}
.btn-box{
	position:fixed;
	bottom:0;
	width:100%;
	text-align:center;
	padding:10px 0;
	left:0;
	z-index:88;
	background:#f1f1f1;
}
.show-cancel-box{
	text-align:center;
	padding:20px 0;
}
.show-cancel-box img{
	margin-top:20px;
}
